@import '../../assets/scss/utils/_module';

.footer-links {
    a {
        display: inline-block;
        margin-right: 21px;
        @include transition(all, 0.3s, ease);

        &:hover {
            opacity: 0.7;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}